<template>
	<view class="content" @scroll='scrollMain'>
        <!-- heads -->
        <heads class="head" :sTop='scrollTop' :title='"录音棚"'></heads>
        
        <!-- 搜索 -->
        <view class="tabView">
            <u-search :style="{width: 686+'rpx'}" placeholder="请输入编号或名称" shape="round" :clearabled="true" :show-action='false'></u-search>
        </view>
        
        <!-- 列表 -->
        <view class="recordingList" :style="{height: listHeight+'px'}">
            <view class="rLi">
                <image class="rlLeft" src="@/static/logo.png"></image>
                <view class="rlRight">
                    <view class="rlrTop">
                        <text>保德路1213弄</text>
                        <view class="rlrtRight">
                            <image src="@/static/sing/hot_0.png"></image>
                            5620
                        </view>
                    </view>
                    
                    <view class="rlrTxt">
                        16㎡ | 南
                    </view>
                    
                    <view class="rlrTags">
                        <view class="rTag">功能强劲</view>
                        <view class="rTag">拍制一体</view>
                        <view class="rTag">交通便利</view>
                    </view>
                    
                    <view class="rlrAddress">
                        <image src="@/static/icon/addrees.png"></image>
                        <text>上海市徐汇区XXXXX</text>
                    </view>
                    
                    <view class="rlrPrice">
                        <text class="red rTxt1">￥</text>
                        <text class="red rTxt2">2500</text>
                        <text class="blak rTxt3">￥</text>
                        <text class="blak rTxt3">2800</text>
                    </view>
                </view>
            </view>
            
            <view class="rLi">
                <image class="rlLeft" src="@/static/logo.png"></image>
                <view class="rlRight">
                    <view class="rlrTop">
                        <text>保德路1213弄</text>
                        <view class="rlrtRight">
                            <image src="@/static/sing/hot_0.png"></image>
                            5620
                        </view>
                    </view>
                    
                    <view class="rlrTxt">
                        16㎡ | 南
                    </view>
                    
                    <view class="rlrTags">
                        <view class="rTag">功能强劲</view>
                        <view class="rTag">拍制一体</view>
                        <view class="rTag">交通便利</view>
                    </view>
                    
                    <view class="rlrAddress">
                        <image src="@/static/icon/addrees.png"></image>
                        <text>上海市徐汇区XXXXX</text>
                    </view>
                    
                    <view class="rlrPrice">
                        <text class="red rTxt1">￥</text>
                        <text class="red rTxt2">2500</text>
                        <text class="blak rTxt3">￥</text>
                        <text class="blak rTxt3">2800</text>
                    </view>
                </view>
            </view>
            
        </view>
        
        <!-- tabbar -->
        <u-tabbar :list="tabbar" :mid-button="true" height='58px' active-color='#FFD3AA'></u-tabbar>
    </view>
</template>

<script>
	export default {
		data() {
			return {
                scrollTop:0,
                windowHeight:0,
                headHeight:0,
                tabViewHeight:0,
			}
		},
        computed: {
            listHeight: function () {
                let num = this.windowHeight - this.headHeight - this.tabViewHeight - 50
                return num.toFixed(2)
            },
            tabbar: function () {
                return getApp().globalData.tabbar
            }
            
        },
		onLoad() {
            uni.getSystemInfo({
              success: (res=> {
                this.windowHeight = res.windowHeight; // 窗口高度
              })
            });
            
            const query = uni.createSelectorQuery().in(this);
                  
            query.select('.head').boundingClientRect(data => {
                if (data) {
                  this.headHeight = data.height
                }
            }).exec();
            
            query.select('.tabView').boundingClientRect(data => {
                if (data) {
                    this.tabViewHeight = data.height
                }
            }).exec(); // 执行查询
            
		},
		methods: {
            
		}
	}
</script>

<style lang="scss" scoped>
    .content{
        width: 100vw;
        height: 100vh;
        overflow: auto;
        background: url('https://image-file2024.oss-cn-beijing.aliyuncs.com/geWoLiMusicMall/backgroundImage/%E6%AD%8C%E7%AA%9D%E9%87%8C%E8%83%8C%E6%99%AF%E5%9B%BE1.png');
        background-size: 100% 100%;
    }
    .tabView{
        width: 686rpx;
        margin: 40rpx auto 32rpx;
        color: #999;
        display: flex;
        align-items: center;
    }
    .recordingList{
        width: 686rpx;
        margin: 0 auto 0;
        overflow: auto;
        .rLi{
            border-radius: 40rpx;
            overflow: hidden;
            display: flex;
            margin-bottom: 20rpx;
            background: #fff;
            box-shadow: 0rpx 0rpx 62rpx 0rpx rgba(255,239,223,0.5);
            .rlLeft{
                width: 250rpx;
                height: 242rpx;
            }
            .rlRight{
                display: flex;
                justify-content: space-between;
                flex-direction: column;
                width: 436rpx;
                padding: 20rpx 40rpx 18rpx 22rpx;
                .rlrTop{
                    display: flex;
                    font-size: 24rpx;
                    font-weight: 500;
                    color: #000000;
                    .rlrtRight{
                        margin-left: auto;
                        font-size: 22rpx;
                        line-height: 44rpx;
                        color: #999999;
                        font-weight: 500;
                        image{
                            width: 31rpx;
                            height: 31rpx;
                            vertical-align: middle;
                            margin-right: 12rpx;
                        }
                    }
                }
                .rlrTxt{
                    color: #999999;
                    font-size: 20rpx;
                }
                .rlrTags{
                    display: flex;
                    .rTag{
                        margin-right: 12rpx;
                        padding: 4rpx 10rpx;
                        background: #E5E5E5;
                        border-radius: 20rpx;
                        color: #999999;
                        font-size: 20rpx;
                    }
                }
                .rlrAddress{
                    font-size: 20rpx;
                    color: #999999;
                    display: flex;
                    align-items: center;
                    image{
                        width: 36rpx;
                        height: 36rpx;
                    }
                }
                .rlrPrice{
                    .red{
                        color: #FC3758;
                        font-size: 28rpx;
                    }
                    .blak{
                        font-size: 20rpx;
                        text-decoration: line-through;
                        color: rgba(153,153,153,0.6);
                    }
                    .rTxt2{
                        display: inline-block;
                        margin-right: 5rpx;
                    }
                }
            }
        }
    }
    
    
</style>
